const maxStep = 8;
const minStep = 1;

export const characterCreator = {
  namespaced: true,
  state: {
    step: 1,
    level: 1,

    //Ability Scores
    strength: 10,
    dexterity: 10,
    constitution: 10,
    intelligence: 10,
    wisdom: 10,
    charisma: 10,
    ancestryBoosts: [],
    ancestryFlaws: [],
    ancestryChosenBoosts: [],

    //Ancestry
    ancestry: null,
    ancestryFeatures: [],
    heritage: null,
    level1AncestryFeat: null,

    //Background
    background: null,
    backgroundAbility1: null,
    backgroundAbility2: null,
    backgroundSkill: null,
    backgroundLore: null,
    backgroundFeat: null,

    //class
    klass: null
  },
  actions: {
    nextStep({commit}){
      commit("nextStep");
    },
    previousStep({commit}){
      commit("previousStep");
    },
    setAncestry({ commit }, ancestry) {
      commit("setAncestry", ancestry);
      commit("calculateAbilities");
    },
    setAncestryBoostedAbilities({ commit }, abilities) {
      commit("setAncestryBoostedAbilities", abilities);
      commit("calculateAbilities");
    },
    setAncestryFeatures({ commit }, features) {
      commit("setAncestryFeatures", features);
    },
    setHeritage({ commit }, heritage) {
      commit("setHeritage", heritage);
    },
    setAncestryFeat1({ commit }, feat) {
      commit("setAncestryFeat1", feat);
    },
    setBackground({ commit }, background) {
      commit("setBackground", background);
    },
    setBackgroundAbility1({ commit }, ability) {
      commit("setBackgroundAbility1", ability);
      commit("calculateAbilities");
    },
    setBackgroundAbility2({ commit }, ability) {
      commit("setBackgroundAbility2", ability);
      commit("calculateAbilities");
    },
    setBackgroundSkill({ commit }, skill) {
      commit("setBackgroundSkill", skill);
    },
    setBackgroundLore({ commit }, lore) {
      commit("setBackgroundLore", lore);
    },
    setBackgroundFeat({ commit }, feat) {
      commit("setBackgroundFeat", feat);
    },
    setClass({ commit }, klass) {
      commit("setClass", klass);
    }
  },
  mutations: {
    nextStep(state){
      if (state.step < maxStep) {
        state.step++;
      }
    },
    previousStep(state){
      if (state.step > minStep) {
        state.step--;
      }
    },
    setAncestry(state, ancestry) {
      state.ancestry = ancestry;
      state.ancestryBoosts = ancestry.abilityBoosts;
      state.ancestryFlaws = ancestry.abilityFlaws;
      state.ancestryChosenBoosts = [];
      state.freeAbilityBoosts = ancestry.freeAbilityBoosts;
    },
    setAncestryBoostedAbilities(state, abilities) {
      state.ancestryChosenBoosts = abilities;
    },
    setAncestryFeatures(state, features) {
      state.ancestryFeatures = features;
    },
    setHeritage(state, heritage) {
      state.heritage = heritage;
    },
    setAncestryFeat1(state, feat) {
     state.level1AncestryFeat = feat;
    },
    calculateAbilities(state) {
      state.strength = 10;
      state.dexterity = 10;
      state.constitution = 10;
      state.intelligence = 10;
      state.wisdom = 10;
      state.charisma = 10;

      for (let boost in state.ancestryBoosts)
        state[state.ancestryBoosts[boost]] += 2;

      for (let flaw in state.ancestryFlaws)
        state[state.ancestryFlaws[flaw]] -= 2;

      for (let boost in state.ancestryChosenBoosts){
        state[state.ancestryChosenBoosts[boost]] += 2;
      }

      if (state.backgroundAbility1){
        state[state.backgroundAbility1] += 2;
      }
      if (state.backgroundAbility2){
        state[state.backgroundAbility2] += 2;
      }
    },
    setBackground(state, background) {
      state.background = background;
    },
    setBackgroundAbility1(state, ability) {
      state.backgroundAbility1 = ability;
    },
    setBackgroundAbility2(state, ability) {
      state.backgroundAbility2 = ability;
    },
    setBackgroundSkill(state, skill) {
      state.backgroundSkill = skill;
    },
    setBackgroundLore(state, lore) {
      state.backgroundLore = lore;
    },
    setBackgroundFeat(state, feat) {
      state.backgroundFeat = feat;
    },
    setClass(state, klass) {
      state.klass = klass;
    }
  }
};
