<template>
  <div class="container text-center">
    <h1>Pathfinder Character Creator</h1>
    <p class="lead">
      Jump into the game quickly and easily with this powerful character
      creator!
    </p>
  </div>
</template>

<script>
import UserService from "../services/user.service";

export default {
  name: "Home",
  data() {
    return {
      content: "",
    };
  },
  mounted() {
    UserService.getPublicContent().then(
      (response) => {
        this.content = response.data;
      },
      (error) => {
        this.content =
          (error.response && error.response.data) ||
          error.message ||
          error.toString();
      }
    );
  },
};
</script>
