<template>
  <div id="app">
    <nav class="navbar navbar-expand navbar-dark">
      <div class="container">
        <router-link to="/" href class="navbar-brand" @click.prevent
          >Brand</router-link
        >
        <div class="navbar-nav mr-auto">
          <li class="nav-item">
            <router-link
              v-if="currentUser"
              to="/character-list"
              class="nav-link"
              >Your Characters</router-link
            >
          </li>
          <li class="nav-item">
            <router-link
              v-if="currentUser"
              to="/character-creator"
              class="nav-link"
              >Create Character</router-link
            >
          </li>
        </div>

        <div v-if="!currentUser" class="navbar-nav ml-auto">
          <li class="nav-item">
            <router-link to="/register" class="nav-link"> Sign Up </router-link>
          </li>
          <li class="nav-item">
            <router-link to="/login" class="nav-link"> Login </router-link>
          </li>
        </div>

        <div v-if="currentUser" class="navbar-nav ml-auto">
          <li class="nav-item">
            <a class="nav-link" href @click.prevent="logOut"> Log Out </a>
          </li>
        </div>
      </div>
    </nav>

    <div class="container">
      <router-view />
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
    showAdminBoard() {
      if (this.currentUser && this.currentUser.roles) {
        return this.currentUser.roles.includes("ROLE_ADMIN");
      }

      return false;
    },
    showModeratorBoard() {
      if (this.currentUser && this.currentUser.roles) {
        return this.currentUser.roles.includes("ROLE_MODERATOR");
      }

      return false;
    },
  },
  methods: {
    logOut() {
      this.$store.dispatch("auth/logout");
      this.$router.push("/login");
    },
  },
};
</script>

<style lang="scss">
@import url('https://fonts.googleapis.com/css2?family=Calistoga&family=Crimson+Pro:wght@700&family=Titillium+Web:ital,wght@0,400;0,700;1,400;1,700&family=Voces&display=swap');

:root {
  --primary: #5e0000 !important;
  --primary-darker: #1c0000;
  --primary-dark: #3a0000;
  --primary-light: #861515;
  --primary-lighter: #af3737;
  --primary-lightest: #ffa5a5;
  --secondary: #ffeebd !important;
  --secondary-dark: #d9c486;
  --secondary-dark: #B39b56;
  --secondary-light: #fff6db;
  --secondary-lighter: #fffcf2;

    --h1: 40px;
    --h2: 28px;
    --h3: 22px;
    --h4: 18px;
}

#app {
  color: #461413;
  font-family: "Titillium Web",sans-serif;
  
  .navbar {
    background-color: var(--primary);
  }

  .navbar-dark .navbar-nav .nav-link {
    color: rgba(255, 255, 255, 0.75);
  }
  .navbar-dark .navbar-nav .nav-link:hover {
    color: rgba(255, 255, 255, 1);
  }

  h1 {
    font-family: "Titillium Web",sans-serif;
    font-size: var(--h1);
    margin: 2rem 0;
    text-align: center;
    font-weight: 700;
  }
  h2 {
    text-transform: uppercase;
    font-family: "Titillium Web",sans-serif;
    font-weight: 700;
    font-size: var(--h2);
    margin-top: 1rem;
  }
  h3 {
    font-size: var(--h3);
    font-family: "Titillium Web",sans-serif;
    font-weight: 700;
    margin-top: 1rem;
  }
  h4 {
    font-size: var(--h4);
    font-family: "Titillium Web",sans-serif;
    font-weight: 700;
  }

  .information {
    background: var(--secondary-light);
    border: 1px solid var(--secondary-dark);
    margin: 2rem 0;
    padding: 1rem;
  }


  .radio-toolbar,
  .checkbox-toolbar {
    input[type="radio"],
    input[type="checkbox"] {
      opacity: 0;
      position: fixed;
      width: 0;
      color: black;

      &:hover + label {
        background-color: var(--secondary-light);
        border-color: var(--primary);
        color: var(--primary);
      }

      &:checked + label {
        background-color: var(--primary-light);
        border-color: var(--primary) !important;
        color: white;
      }

      &:disabled + label {
        background-color: #f0f0f0;
        border-color: #444;
        color: #444;
        cursor: default;
      }
    }

    label {
      display: inline-block;
      background-color: transparent;
      padding: 1rem;
      font-family: sans-serif, Arial;
      font-size: 16px;
      color: var(--primary);
      border: 1px solid var(--primary);
      border-radius: 4px;
      text-align: center;
      cursor: pointer;
    }
  }

  .theme--light.v-stepper .v-stepper__step__step.primary {
    background: var(--primary);
  }

}
</style>
